___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --color-primary: #eeeeee;
  --color-primary-white: #ffffff;
  --color-primary-light: #f9f9f9;
  --color-primary-dark: rgb(33, 36, 41);
  --color-brand-normal: #819b57;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.5;
  margin: 0;
  color: var(--gray-darker);
  background-color: #eeeeee;
  background-color: var(--color-primary);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  -o-object-fit: cover;
     object-fit: cover;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  display: inline-block;
}

address {
  font-style: normal;
}

.link {
  color: #f9f9f9;
  color: var(--color-primary-light);
  text-decoration: none;
  transition: color 0.3s;
}
.link:hover {
  color: #819b57;
  color: var(--color-brand-normal);
}

.marquee-container {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  background: #222;
  padding: 10px;
  position: relative;
}
.marquee-container__marquee {
  display: inline-block;
  white-space: nowrap;
  color: white;
  font-size: 20px;
  font-weight: bold;
  animation: marquee-scroll 10s linear infinite;
}

@keyframes marquee-scroll {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(-100%);
  }
}
.benefits {
  display: flex;
  justify-content: space-around;
  padding: 12px 0 12px 18px;
  background: linear-gradient(92deg, #00d4f7 13.54%, rgb(221, 174, 65) 85.29%);
}
.benefits__item {
  display: flex;
  font-size: 12px;
  font-weight: 800;
  align-items: center;
  width: 30%;
  gap: 8px;
}
.benefits__icon-container {
  background: rgb(33, 36, 41);
  background: var(--color-primary-dark);
  border-radius: 50%;
  padding: 8px;
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
}
.benefits__icon {
  fill: #eeeeee;
  fill: var(--color-primary);
}

.container {
  width: 100%;
  max-width: 480px;
  margin: 0 auto;
  background-color: #ffffff;
  background-color: var(--color-primary-white);
}

.footer {
  padding: 32px 0;
  background-color: rgb(33, 36, 41);
  background-color: var(--color-primary-dark);
}
.footer__container {
  display: flex;
  gap: 18px;
  flex-direction: column;
  cursor: default;
}
.footer__title {
  margin-bottom: 14px;
}
.footer__address {
  display: flex;
  flex-direction: column;
  gap: 24px;
  text-align: center;
}
.footer__address-contacts {
  color: #f9f9f9;
  color: var(--color-primary-light);
}
.footer__address-list, .footer__address-info {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.footer__address-list--item {
  display: flex;
  gap: 8px;
  justify-content: center;
}
.footer__address-link {
  font-size: 16px;
  font-weight: 300;
  opacity: 0.8;
}
.footer__address-link--policy {
  margin-top: 12px;
}
.footer__address-link:hover {
  color: #819b57;
  color: var(--color-brand-normal);
}
.footer__schedule {
  font-size: 16px;
  color: #f9f9f9;
  color: var(--color-primary-light);
}
.footer__address-copyright {
  color: #f9f9f9;
  color: var(--color-primary-light);
  font-size: 12px;
  opacity: 0.6;
  margin-top: 10px;
}

.header__title {
  padding: 15px 20px 18px 20px;
  text-align: center;
  font-size: 30px;
  line-height: 44px;
  font-weight: 700;
  text-transform: uppercase;
}

.hero {
  height: 518px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-position: center center;
}

.price {
  display: flex;
  border-bottom: 1px solid #e6e6e6;
  overflow: hidden;
  margin-bottom: 15px;
